import { short } from '../../settings/animation';
import { CLASS_OPEN } from '../../tools/constants/classes';
import { slideDown, slideUp } from '../../tools/utilities/animation-tools';
import { findTarget } from '../../tools/utilities/event-tools';

// Selectors
const SELECTOR_MENU = '[data-navigation-mobile]';
const SELECTOR_SUBMENU_CONTAINER = '[data-mobile-submenu-container]';
const SELECTOR_SUBMNEU_TOGGLE = '[data-mobile-submenu-toggle]';
const SELECTOR_SUBMENU = '[data-mobile-submenu]';

/**
 * Handles the functionality of toggling the submenu when the user clicks
 * the submenu toggle.
 * @param e The mouse click event for when the user clicks the toggle
 * @param menu The menu html element of that contains the clicked toggle
 * @returns void
 */
const onSubmenuToggle = (e:Event, menu:HTMLElement) => {
  e.preventDefault();

  const target = findTarget(e, SELECTOR_SUBMNEU_TOGGLE);
  if(!target) return;

  const container = target.closest<HTMLElement>(SELECTOR_SUBMENU_CONTAINER);
  if(!container) return;

  const submenu = container.querySelector<HTMLElement>(SELECTOR_SUBMENU);
  if(!submenu) return;

  // If the container is open, close it. If the container is closed, open it.
  if(container.classList.contains(CLASS_OPEN)) {
    slideUp(submenu);
    container.classList.remove(CLASS_OPEN);
  } else {
    // Close any open menus
    const activeContainers = menu.querySelectorAll<HTMLElement>(`${SELECTOR_SUBMENU_CONTAINER}.${CLASS_OPEN}`);
    activeContainers.forEach(activeContainer => {
      const activeSubmenu = activeContainer.querySelector<HTMLElement>(SELECTOR_SUBMENU);
      if(!activeSubmenu) return

      slideUp(activeSubmenu);
      activeContainer.classList.remove(CLASS_OPEN);
    });

    // Open the clicked on menu
    slideDown(submenu, short, 'flex');
    container.classList.add(CLASS_OPEN);
  }
}

document.querySelectorAll<HTMLElement>(SELECTOR_MENU).forEach(elementMenu => {
  elementMenu.querySelectorAll<HTMLAnchorElement>(SELECTOR_SUBMNEU_TOGGLE).forEach(elementToggle => {
    elementToggle.addEventListener('click', (e) => onSubmenuToggle(e, elementMenu));
  });
});