import { short } from '../../settings/animation';

/**
 * Creates a slide up animation effect
 * @param target The html element the animation is targeting
 * @param duration The number of miliseconds to use as the duration of the animation
 */
 export const slideUp = (target:HTMLElement, duration:number = short) => {
  target.style.transitionProperty = 'height, margin, padding';
  target.style.transitionDuration = duration + 'ms';
  target.style.boxSizing = 'border-box';
  target.style.height = target.offsetHeight + 'px';
  target.offsetHeight;
  target.style.overflow = 'hidden';
  target.style.height = '0px';
  target.style.paddingTop = '0px';
  target.style.paddingBottom = '0px';
  target.style.marginTop = '0px';
  target.style.marginBottom = '0px';

  setTimeout(() => {
    target.style.display = 'none';
    target.style.removeProperty('height');
    target.style.removeProperty('padding-top');
    target.style.removeProperty('padding-bottom');
    target.style.removeProperty('margin-top');
    target.style.removeProperty('margin-bottom');
    target.style.removeProperty('overflow');
    target.style.removeProperty('transition-duration');
    target.style.removeProperty('transition-property');
  }, duration);
}

/**
 * Creates a slide down animation effect
 * @param target The html element the animation is targeting
 * @param duration The number of miliseconds to use as the duration of the animation
 */
export const slideDown = (target:HTMLElement, duration:number = short, displayType:string = 'block') => {
  target.style.removeProperty('display');
  let display = window.getComputedStyle(target).display;
  if (display === 'none') display = displayType;
  target.style.display = display;
  let height = target.offsetHeight;
  target.style.overflow = 'hidden';
  target.style.height = '0px';
  target.style.paddingTop = '0px';
  target.style.paddingBottom = '0px';
  target.style.marginTop = '0px';
  target.style.marginBottom = '0px';
  target.offsetHeight;
  target.style.boxSizing = 'border-box';
  target.style.transitionProperty = "height, margin, padding";
  target.style.transitionDuration = duration + 'ms';
  target.style.height = height + 'px';
  target.style.removeProperty('padding-top');
  target.style.removeProperty('padding-bottom');
  target.style.removeProperty('margin-top');
  target.style.removeProperty('margin-bottom');

  setTimeout( () => {
    target.style.removeProperty('height');
    target.style.removeProperty('overflow');
    target.style.removeProperty('transition-duration');
    target.style.removeProperty('transition-property');
  }, duration);
}

/**
 * Toggles an the targeted element with either a slide up or slide down animation
 * @param target The html element the animation is targeting
 * @param duration The number of miliseconds to use as the duration of the animation
 */
export const slideToggle = (target:HTMLElement, duration:number = short, displayType:string = 'block') => {
  if (window.getComputedStyle(target).display === 'none') {
    return slideDown(target, duration, displayType);
  } else {
    return slideUp(target, duration);
  }
}