/*
 *  Navigation Desktop
 *    Scripts for the desktop navigation
 *
 *  Version:
 *    1.0.0 - 2021/12/14
 */

import { responsiveMediaBelow } from '../../settings/responsive';
import { CLASS_ACTIVE, CLASS_OVERLAY_OPEN } from '../../tools/constants/classes';
import { findTarget } from '../../tools/utilities/event-tools';

// Selectors
const SELECTOR_NAV_CONTAINER = '[data-navigation-desktop]';
const SELECTOR_LVL1_ITEM = '[data-lvl1-item]';

// Elements
const ELEMENT_BODY = document.body;

/**
 * Handles the functionality for adding the overlay and transition delay classes.
 * @param element The element containing the inner submenu
 */
const onMouseOver = (element:HTMLElement) => {
  if(responsiveMediaBelow('MEDIUM')) return;

  const container = element.closest<HTMLElement>(SELECTOR_NAV_CONTAINER);
  if(!container) return;

  // A split delay to let the css hover effect kick in first
  setTimeout(() => {
    if(container.classList.contains(CLASS_ACTIVE)) return;
    container.classList.add(CLASS_ACTIVE);
  }, 100);

  ELEMENT_BODY.classList.add(CLASS_OVERLAY_OPEN);
}

/**
 * Handles the functionality for removing the overlay and transition delay classes.
 * @param element The element containing the inner submenu
 */
const onMouseLeave = (element:HTMLElement) => {
  if(responsiveMediaBelow('MEDIUM')) return;

  const container = element.closest<HTMLElement>(SELECTOR_NAV_CONTAINER);
  if(!container) return;

   // A split delay to let the css hover effect kick in first
  setTimeout(() => {
    if(!container.classList.contains(CLASS_ACTIVE)) return;
    container.classList.remove(CLASS_ACTIVE);
  }, 100);

  ELEMENT_BODY.classList.remove(CLASS_OVERLAY_OPEN);
}

// Menu overlay hover events
document.querySelectorAll<HTMLElement>(SELECTOR_LVL1_ITEM).forEach(element => {
  element.addEventListener('mouseover', () => onMouseOver(element));
  element.addEventListener('mouseleave', () => onMouseLeave(element));
});

// Close menu if hovering on anything outside the menu
document.addEventListener('DOMContentLoaded', () => {
  document.addEventListener('mouseover', (e) => {
    const target = findTarget(e, SELECTOR_LVL1_ITEM);
    if(target) return;

    document.querySelectorAll<HTMLElement>(`${SELECTOR_LVL1_ITEM}`).forEach(element => {
      onMouseLeave(element);
    });
  })
});