export type ShopifyBrowsingContext = {
  detected_values: {
    country_name: string;
    country: {
      handle: string;
      name: string;
    }
  },
  suggestions: string[]|[];
}

export const getShopifyLocation = async () : Promise<ShopifyBrowsingContext|null> => {
  let locationObject = null;
  try {
    locationObject = await fetch('/browsing_context_suggestions.json')
    .then(r => r.json());
  } catch (error) {
    console.warn(error);
  }

  return locationObject;
}
