import { getCurrentCart, jq, ON_CART_FINISHED } from "@process-creative/slate-theme-tools";
import { CLASS_ACTIVE, CLASS_FILTERED, CLASS_INITIALISED, CLASS_STICKY, CLASS_VISIBLE, CLASS_WAITING } from "../../tools/constants/classes";
import { documentDisableScroll, documentEnableScroll } from "../../tools/layout/document";
import { getShopifyLocation } from "../../tools/utilities/geolocation-tools";
import KeenSlider from 'keen-slider';
import {
  CarouselOptions,
  initCarousel,
  initCarouselParams,
} from '../../objects/carousel';

// Attributes
const ATTR_SLIDE_LOCTION = 'data-promobar-slide-location';
const ATTR_AUTOPLAY_SPEED = 'data-autoplay-speed';
const ATTR_AUTOPLAY = 'data-autoplay';

// Selectors
const SELECTOR_HEADER_CONTAINER = '[data-header-container]';
const SELECTOR_HEADER = '[data-header]';

const SELECTOR_PROMOBAR = '[data-promobar]';
const SELECTOR_PROMOBAR_SLIDE = '[data-promobar-slide]';
const SELECTOR_PROMOBAR_CAROUSEL = '[data-promobar-carousel]';
const SELECTOR_SLIDER_NAV = '[data-carousel-navigation]';

const SELECTOR_NAVIGATION_MOBILE_TOGGLE = '[data-mobile-menu-toggle]';
const SELECTOR_NAVIGATION_MOBILE_INNER = '[data-navigation-mobile-inner]';
const SELECTOR_NAVIGATION_MOBILE_OVERFLOW = '[data-navigation-mobile-overflow]';

const SELECTOR_SEARCH_TOGGLE = '[data-search-toggle]';
const SELECTOR_SEARCH_INPUT = '[data-search-input]';
const SELECTOR_SEARCH_FORM = '[data-search-form]';

const SELECTOR_CART_COUNT = '[data-cart-count]';

const SELECTOR_CURRENCY_SELECT = '[data-currency-selector]';

const CLASS_NO_CAROUSEL = 'is-no-carousel';

let previousTop = 0;
const elementHeader = document.querySelector(SELECTOR_HEADER);
const elementHeaderContainer = document.querySelector<HTMLElement>(SELECTOR_HEADER_CONTAINER);

/**
 * Finds out the users country via the Shopify browser context endpoint. Then uses
 * that country to filter down the promo slides to be specific to the correct location.
 * @param slides A node list of html elements of the promo slides to filter.
 * @returns A promise that returns the number of remaining slides after filtering.
 */
const filterPromosByLocation = (slides: NodeListOf<HTMLElement>): Promise<number> => {
  return new Promise(async (resolve, reject) => {
    const userCountryObject = await getShopifyLocation();

    // Check that we have a country object
    if (!userCountryObject) {
      reject('Failed to get country object');
      return;
    }

    // Check that there is a country code
    const country_code = userCountryObject.detected_values.country.handle.toLowerCase();
    if (!country_code) reject('No country code available');

    let slidesToDeduct = 0;

    // Remove slides depending on the user country
    if (country_code == 'au') {
      // Remove slides that aren't au or all locations
      slides.forEach(slide => {
        const slideLocation = slide.getAttribute(ATTR_SLIDE_LOCTION);
        if (!slideLocation) slide.remove();

        if (slideLocation != country_code && slideLocation != 'all') {
          slide.remove();
          slidesToDeduct++;
        }
      });
    } else {
      // Remove slides that aren't international or all locations
      slides.forEach(slide => {
        const slideLocation = slide.getAttribute(ATTR_SLIDE_LOCTION);
        if (!slideLocation) slide.remove();

        if (slideLocation != 'int' && slideLocation != 'all') {
          slide.remove();
          slidesToDeduct++;
        }
      });
    }

    resolve(slides.length - slidesToDeduct);
  });
}

/**
 * Initialises the topbar carousel using keen slide
 * Allows client to set the speed and autoplay for the carousel
 * @param container The html element containing the navigation and carousel elements
 */

function initialiseTopbarCarousel() {
  // Get the carousel
  const topbar = document.querySelector<HTMLElement>(SELECTOR_PROMOBAR);
  const topBarCarousel = document.querySelector<HTMLElement>(SELECTOR_PROMOBAR_CAROUSEL);
  if (!topbar || !topBarCarousel) return;

  //get the carousel settings
  const speed = topbar.getAttribute(ATTR_AUTOPLAY_SPEED);
  const autoplay = topbar.getAttribute(ATTR_AUTOPLAY);

  //carousel settings
  const settings = {
    autoplay: autoplay === 'true' ? true : false,
    autoplaySpeed: speed ? parseInt(speed) : 3
  };

  // Initialise the carousel
  const params: initCarouselParams = {
    container: topBarCarousel,
    options: {
      ...settings,
      loop: true
    }
  }

  // Navigation for the carousel
  const navigationContainer = topbar.querySelector<HTMLElement>(SELECTOR_SLIDER_NAV);
  params.navigationContainer = navigationContainer;

  const slider = initCarousel(params);

  topbar.classList.add(CLASS_INITIALISED);
}

/**
 * The promobar functionality for the header
 */
const elementPromobar = document.querySelectorAll<HTMLElement>(SELECTOR_PROMOBAR);
elementPromobar.forEach(async element => {
  if (element.classList.contains(CLASS_INITIALISED)) return;
  element.classList.add(CLASS_INITIALISED);

  const elementPromobarCarousel = element.querySelector<HTMLElement>(SELECTOR_PROMOBAR_CAROUSEL);
  if (!elementPromobarCarousel) return;

  const elementPromobarSlides = elementPromobarCarousel.querySelectorAll<HTMLElement>(SELECTOR_PROMOBAR_SLIDE);
  if (!elementPromobarSlides) return;

  // Filter slides by user country
  const remainingSlidesLength = await filterPromosByLocation(elementPromobarSlides);
  elementPromobarCarousel.classList.add(CLASS_FILTERED);

  // If theres only 1 slide then we don't need to initialise the carousel
  if (remainingSlidesLength <= 1) {
    element.classList.add(CLASS_NO_CAROUSEL);
    return;
  }

  // Initialise fading carousel
  initialiseTopbarCarousel();
});

/**
 * Handles the functionality of toggling the mobile menu when the user clicks
 * the mobile menu toggle
 * @param e The mouse click event for when the user clicks the toggle
 */
const onMobileMenuToggle = (e?: Event) => {
  if (e) e.preventDefault();

  const menuContainer = document.querySelector<HTMLElement>(SELECTOR_NAVIGATION_MOBILE_INNER);
  if (!menuContainer) return;

  /**
   * For when the header isn't sticky, scroll so that the header is align with
   * the top of the screen
   */
  const promoBar = document.querySelector<HTMLElement>(SELECTOR_PROMOBAR);
  if (promoBar && elementHeader && !elementHeader.classList.contains(CLASS_STICKY)) {
    // If the promo bar exists we need to offset the scroll
    const topOffset = promoBar.clientHeight;

    // Scroll to "top"
    window.scrollTo({
      top: topOffset,
      left: 0,
      behavior: 'smooth'
    });
  }

  // Open / close functionality. We'll also set the height based on the header.
  const header = document.querySelector<HTMLElement>(SELECTOR_HEADER);
  if (!header) return;

  if (header.classList.contains(CLASS_ACTIVE)) {
    documentEnableScroll();
    menuContainer.style.height = '0px';
    header.classList.remove(CLASS_ACTIVE);
  } else {
    documentDisableScroll();
    const newHeight = `calc(100vh - ${header.offsetHeight}px)`;
    menuContainer.style.height = newHeight;
    header.classList.add(CLASS_ACTIVE);
  }
}

// The mobile menu toggle functionality
const mobileMenuToggles = document.querySelectorAll<HTMLButtonElement>(SELECTOR_NAVIGATION_MOBILE_TOGGLE);
mobileMenuToggles.forEach(toggleButton => {
  toggleButton.addEventListener('click', onMobileMenuToggle);
});

/**
 * Handles the functionality of toggling the mobile menu open and focusing the
 * search input.
 * @param e The mouse click event for when the user clicks the toggle
 */
const onMobileSearchToggle = (e: Event) => {
  e.preventDefault();

  const menuContainer = document.querySelector<HTMLElement>(SELECTOR_NAVIGATION_MOBILE_INNER);
  if (!menuContainer) return;

  const header = document.querySelector<HTMLElement>(SELECTOR_HEADER);
  if (!header) return;

  // Check that it's not open
  if (header.classList.contains(CLASS_ACTIVE)) return;

  // Open the menu
  onMobileMenuToggle();

  // Focus the input
  const searchInput = menuContainer.querySelector<HTMLInputElement>(SELECTOR_SEARCH_INPUT);
  if (!searchInput) return;
  searchInput.focus();

  // Scroll the overflow container up to the top so that the search is in view
  const overflowContainer = menuContainer.querySelector<HTMLElement>(SELECTOR_NAVIGATION_MOBILE_OVERFLOW);
  if (!overflowContainer) return;
  overflowContainer.scrollTo({
    top: 0,
    left: 0
  });
}

/**
 * Handles the functionality of toggling the desktop search form
 * @param e The mouse click event for when the user clicks the toggle
 */
const onDesktopSearchToggle = (e: Event) => {
  const searchForm = document.querySelector<HTMLElement>(`${SELECTOR_SEARCH_FORM}.is-desktop`);
  if (!searchForm) return;

  const searchInput = searchForm.querySelector<HTMLInputElement>(SELECTOR_SEARCH_INPUT);
  if (!searchInput) return;

  e.preventDefault();

  if (!searchForm.classList.contains(CLASS_ACTIVE)) {
    searchInput.focus();
    searchInput.select();
  } else {
    searchInput.blur();
  }

  searchForm.classList.toggle(CLASS_ACTIVE);
}

// The search toggle functionality
const searchToggles = document.querySelectorAll<HTMLAnchorElement>(SELECTOR_SEARCH_TOGGLE);
searchToggles.forEach(searchToggle => {
  if (searchToggle.classList.contains('is-mobile')) {
    searchToggle.addEventListener('click', onMobileSearchToggle);
  } else {
    searchToggle.addEventListener('click', onDesktopSearchToggle);
  }
});

/**
 * Controls the header sticky behaviour.
 * - Header is sticky if the user has scrolled past the header height
 * - Header returns to default state when the promobar comes back into view
 * - There are some additional states to manage the animation smoothly
 */
const checkHeaderPosition = () => {
  if (!elementHeader || !elementHeaderContainer) return;

  const elementPromobar = document.querySelector<HTMLElement>(SELECTOR_PROMOBAR);
  if (!elementPromobar) return;

  // Plus 12px to accomodate curve svg
  const headerContainerDistance = elementHeaderContainer.clientHeight + 12;
  const promoBarDistance = elementPromobar.clientHeight;

  let top = window.scrollY;

  // Decide whether the header is in a sticky state or not
  if (top > headerContainerDistance) {
    elementHeader.classList.add(CLASS_STICKY);
  } else if (top < promoBarDistance) {
    elementHeader.classList.remove(CLASS_STICKY, CLASS_WAITING);
  }

  // Are we scrolling up or down?
  if (top < previousTop && top > headerContainerDistance) { // Top
    elementHeader.classList.add(CLASS_WAITING, CLASS_VISIBLE);
  } else if (top > previousTop && top > headerContainerDistance) { // Down
    elementHeader.classList.remove(CLASS_VISIBLE);
  }

  // Reset previous top
  previousTop = top;

  requestAnimationFrame(checkHeaderPosition);
}
checkHeaderPosition();

/**
 * Controls the cart count number.
 * - Updates whenever the cart changes.
 */
const onCartUpdate = () => {
  const countElement = document.querySelector<HTMLElement>(SELECTOR_CART_COUNT);
  if (!countElement) return;

  const count = getCurrentCart()['item_count'];

  countElement.innerHTML = count;

  if (count > 0) {
    countElement.classList.add(CLASS_VISIBLE);
  } else {
    countElement.classList.remove(CLASS_VISIBLE);
  }
}
jq(document).on(ON_CART_FINISHED, () => onCartUpdate());

